ul li {
    width: 90%;
    height: 80px;
    margin: 0% 3%;
    background-color: rgb(101, 101, 101);
    border-radius: 10px;
    display: flex;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(2, 2, 2, 0.15);
  }

@media (max-width: 520px) {
  ul li {
    width: 90%;
    height: 80px;
    margin: 0% -2%;
    background-color: rgb(101, 101, 101);
    border-radius: 10px;
    display: flex;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(2, 2, 2, 0.15);
  }
}